body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body{
  font-family: 'Montserrat', sans-serif;
}

input{
  width: 300px;
  height: 30px;
  font-weight: bold;
  border-radius: 20px;
  padding: 0 10px 0 10px;
  margin:5px;
  border-color: aquamarine;
}
.form-container{
  width: 400px;
  margin: auto;
  display: block;
}
.form-body{
  text-align: center;
  border: 1px solid;
  width: 400px;
  margin: auto;
  padding: 10px 0 0 0;
  display: block;
  background-image: linear-gradient(141deg, #9fb8ad 0%, #1fc8db 51%, #2cb5e8 75%);
}
.sn{
  width: 20px;
}
.uname{
  width: 200px;
}
.num{
  width: 200px;
}
.list{
  text-align: center;
  width : 500px;
  margin: auto;
  display: block;
}
.list-container{
  width : 500px;
  margin: auto;
  display: block;
}

.hbutton{
  margin: 0 0 10px 0;
}
